import React from "react"
import { graphql, Link } from "gatsby"
import { css } from "@emotion/react"
import Layout from "../components/layout"
import Seo from "../components/seo"

const Home = ({ data, pageContext }) => {
  const post = data.allMarkdownRemark
  const { currentPage, numPages } = pageContext
  const isFirst = currentPage === 1
  const isLast = currentPage === numPages
  const prevPage = currentPage - 1 === 1 ? "/" : (currentPage - 1).toString()
  const nextPage = (currentPage + 1).toString() //追加

  const prevPath = prevPage === "/" ? "" : "/page/"
  const nextPath = "/page/"
  
  const pageTitle = currentPage === 1 ? "" : `PAGE${currentPage}`
  const seoTitle = `プログラミングやJavaScriptの備忘録 ${pageTitle}`
  // const prevPath = prevPage === "/" ? "/blog" : "/blog/"
  // const nextPath = "/blog/"
  return (
    <Layout>
      <Seo title={seoTitle} description="Web発Graphic経由Dev行きの鈍行列車" />
      <div
        css={css`
          @media (max-width: 930px) {
            width: 100%;
            padding-right: 0rem;
          }
          box-sizing: border-box;
          float: left;
          margin-bottom: 3rem;
          padding-right: 3rem;
          width: 65%;
        `}
      >
        <h2
          css={css`
            display: inline-block;
            font-size: 2rem;
            margin-bottom: 3rem;
            padding-top: 0;
          `}
        >
          {/* プログラミングに悪戦苦闘する */}
          ひとり開発の春はまだ遠い
        </h2>
        <div
          css={css`
            //font-size: 1rem;
            margin-bottom: 1rem;
          `}
        >
          {currentPage} / Total &nbsp;
          {post.totalCount}
        </div>
        {post.edges.map(({ node }) => (
          <div key={node.id}>
            <h3
              css={css`
                margin-bottom: 0.5rem;
                padding-top: 0;
              `}
            >
              <Link
                to={node.fields.slug}
                css={css`
                  font-weight: 400;
                  color: inherit;
                  text-decoration: none;
                `}
              >
                {node.frontmatter.title}{" "}
              </Link>
            </h3>
            <p
              css={css`
                color: hsla(0, 0%, 0%, 0.6);
                font-size: 0.9rem;
                margin-bottom: 1.56rem;
              `}
            >
              <span
                css={css`
                  color: hsla(0, 0%, 0%, 0.6);
                `}
              >
                — {node.frontmatter.date}
              </span>
              <br />
              {node.excerpt}
            </p>
          </div>
        ))}
        <div
          css={css`
            margin-top: 4rem;
            margin-bottom: 8rem;
            width: 100%;
          `}
        >
          <div
            css={css`
              box-sizing: border-box;
              float: left;
              width: 50%;
            `}
          >
            {!isFirst && (
              <Link to={`${prevPath}${prevPage}`} rel="prev">
                ← Prev
              </Link>
            )}
          </div>
          <div
            css={css`
              box-sizing: border-box;
              float: right;
              text-align: right;
              width: 50%;
            `}
          >
            {!isLast && (
              <Link to={`${nextPath}${nextPage}`} rel="next">
                Next →
              </Link>
            )}
          </div>
        </div>
      </div>
      {/* 省略 */}
    </Layout>
  )
}

export const pageQuery = graphql`
  query HomeQuery($skip: Int!, $limit: Int!) {
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip #追加
    ) {
      totalCount
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            date(formatString: "YYYY.MM.DD")
          }
          excerpt(pruneLength: 150)
        }
      }
    }
  }
`
export default Home
